}
@media (width >= 1440px) and (hover: hover) {
  .header--index .nav__link:not(span):hover, .header--index .nav__link:not(span):focus-visible {
    color: var(--color-font-bg-hover);
  }
}
@media (width >= 1440px) {
  .header--index .nav__link:not(span):active {
    color: var(--color-font-bg-active);
  }
}
@media (width >= 1440px) {
  .header--index .nav__link--current::after {
    box-shadow: 0 6px 0 1px var(--color-bd-bg);
  }
}
@media (hover: hover) {
  .nav__link:not(span):hover, .nav__link:not(span):focus-visible {
    color: var(--color-font-second-hover);
  }
}
.nav__link:not(span):active {
